.header-nav-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    a {
        color: white;
        font-weight: bolder;
        text-decoration: none;
        text-shadow: none;
        transition: text-shadow 200ms;

        &:hover {
            text-shadow: 0 0 1.5rem white;
        }
        
    }

    &__item {
        &:not(:last-child):after {
            content: "|";
            margin: 1rem;
        }

        &--active {
            a {
                color: #61dafb;
            }
        }
    }
}