.privacy-list-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    a {
        color: white;
        font-weight: bolder;
        text-decoration: none;
        margin-bottom: 1rem;

        &:hover {
            color: #61dafb;
        }
        
    }

    &__item {
        margin-bottom: 1rem;
        &--active {
            a {
                color: #61dafb;
            }
        }
    }
}