$flex-container-width: 50rem;
$flex-container-max-width: 100vw;

$color-light: #aaaaaa;
$color-lightest-gray: #484c54;
$color-light-gray: #383c44;
$color-gray: #282c24;
$color-dark-gray: #181c14;
$color-shadow: #383c44;
$color-red: #FF6360;
$color-blue: #61dafb;

$transition-def: all .3s;
$shadow-def: 0 0.1rem 1rem $color-lightest-gray inset, 0 0.1rem 1rem $color-gray;
$header-height: 5rem;