@import '../../styles/variables.scss';

.aside-container {
    position: fixed;
    left: 0;
    top: $header-height;
    bottom: 0;
    width: 500px;
    display: flex;    
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    transition: $transition-def;
    background-color: $color-light-gray;
    
    &--hidden {
        left: -500px;
        transition: $transition-def;
    }

}