@import '../../styles/variables.scss';

.header-menu-container {
    button {
        margin-left: 1rem;
        background-color: transparent;
        border:none;

        &:hover {
            cursor: pointer;
        }

        &:hover, &:focus, &:active {
            svg {
                fill: white;
                transition: $transition-def;
            }
        }

        svg {
            fill: $color-light;
            transition: $transition-def;
        }
    }
}