@import '../../styles/variables.scss';

.scroll-top-button {
    position: fixed;
    right: 0;
    bottom: 0;

    button {
        position: absolute;
        right: 1rem;
        bottom: 1rem;
        width: 5rem;
        height: 5rem;
        background-color: $color-lightest-gray;
        border: none;
        border-radius: 2.5rem;
        opacity: 0.5;

        &:hover {
            opacity: 1.0;
            transition: $transition-def;
            cursor: pointer;
        }

        &:active {
            border: none;
        }

        svg {
            fill: white;
            transition: $transition-def;
        }
    }
}