@import '../../styles/variables.scss';

.header-container {
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    top: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    min-height: $header-height;
    max-height: $header-height;
    background-color: #383c44;
    box-shadow: 0rem -1rem 1rem #61dafb;
    color: white;
}