.logo-container {
    h1 {
        margin: 1rem;
        padding: 0;

        a {
            font-family: 'Lobster', cursive;
            color: #EF5350;
            text-decoration: none;
            transition: color 500ms, text-shadow 500ms;

            &:hover {
                color: #FF6360;
                text-shadow: 0 0 1.5rem  #ffffff88;
            }
        }
    }
}