@import '../../styles/variables.scss';

$radius-def: 1rem;
$border-width-def: 4px; //1px
$border-def: $border-width-def solid $color-gray;


@mixin product-container {
    margin: 1rem;
    background-color: $color-light-gray;
    border-radius: 0;//$radius-def;
    border: $border-def;
    box-shadow: 0 0.1rem 1rem $color-shadow;
    //padding: 1rem;
    border-image: $border-gradient 1;

    .left-panel, .right-panel, .spaced-panel {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: stretch;

        &--portrait {
            flex-wrap: wrap;
        }
    }

    .right-panel {
        flex-direction: row-reverse;
    }

    .spaced-panel {
        justify-content: space-between;
    }

    .product-title , .product-description, .product-screenshot {
        margin: 1rem;
    }

    .product-title {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        padding: 1rem;
        background-color: $color-lightest-gray;
        border: $border-def;
        border-image: $border-gradient 1;
        box-shadow: 0 0.1rem 1rem $color-lightest-gray inset, 0 0.1rem 1rem $color-gray;

        h2 {
            margin: 0;
            padding: 0;
            text-transform: uppercase;
            //background: $border-gradient;
            //background-clip: text;
            //-webkit-background-clip: text;
            //-webkit-text-fill-color: transparent;
        }
    }

    .product-description {
        background-color: $color-lightest-gray;
        border: $border-def;
        border-image: $border-gradient 1;
        //padding: 1rem;
        box-shadow: 0 0 1rem $color-gray inset;

        p {
            margin: 1rem;
            color: white;
        }
    }

    .product-screenshot {
        border: $border-def;
        border-radius: $radius-def;
        border-image: $border-gradient 1;

        img {
            width: 100%;
            max-width: 15rem;
            height: auto;
            cursor: zoom-in;
        }

        .product-screenshot__modal {
            position: fixed;
            left: 0;
            top: 0;
            width: 100vw;
            height: 100vh;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #000a;
            z-index: 1040;
            cursor: zoom-out;

            img {
                cursor: zoom-out;
                width: auto;
                height: auto;
                max-width: 100vw;
                max-height: 100vh;
                border: none;
                border-radius: 0;
            }
        }
    }

    .google-badge {
        width: 100%;
        max-width: 16rem;
        height: auto;
    }
}