.aside-nav-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    margin: 1rem;

    a {
        color: white;
        font-weight: bolder;
        text-decoration: none;
        text-shadow: none;
        transition: text-shadow 200ms;

        &:hover {
            text-shadow: 0 0 1.5rem white;
        }
        
    }

    &__item {
        margin-bottom: 1rem;
        &--active {
            a {
                color: #61dafb;
            }
        }
    }
}