@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

$radius-def: 1rem;
$border-def: 1px solid $color-gray;

.home-about-container {
    @include flex-container;
       
    .about-panel {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: stretch;
        margin: 1rem 1rem 0rem 1rem;
        padding: 1rem;
        background-color: $color-light-gray;
        border: $border-def;
        border-radius: $radius-def;
        box-shadow: 0 0.1rem 1rem $color-light-gray inset, 0 0.1rem 1rem $color-gray;

        h2 {
            margin: 0.5rem;
            color: $color-red;
        }
        p {
            margin: 0.5rem ;
            color: white;
        }
    }
}